<template>
    <Header :homeDisplay="homeDisplay"/>
    <h3>메뉴입력 창입니다</h3>
    <div class="tablepick">
        <form @submit.prevent="menuHandleSubmit">
            <div class="mycal">
                <div class="justify-content-center">
                    <VDatePicker :attributes='attributes' @dayclick="onDayClick" mode="multiple" 
                    :disabled-dates="disabledDates" :color="selectedColor" :min-date='new Date()' 
                    :disabled-attribute='disabledAttribute' class="my-calendar"
                    />
                </div>
            </div>

        <div class="form-group mb-2" v-if="input_menu_display===1">
            <label class="sr-only">메뉴 입력</label>
            <input type="text" v-model="this.message" placeholder="탕수육">
            <button @click="inputMeal">확인</button>
        </div>
        
            
            <!-- <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.message">
                {{ this.message }}
            </div>

            <div class="footer" v-if="input_menu_display!=1">
                <div class="d-flex justify-content-center">
                    <button @click="this.inputMenu()" >설정</button>
                </div>
            </div> -->
        </form>
    </div>
</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";

var holidays= ref([]);
var edit= ref([]);
var input_menu_display=ref(0);
var pick_date=ref('');
var pick_id = ref('');
// var newMeal= ref([]);

const selectedColor = ref('white');

const disabledDates = ref([{
        repeat: {
            weekdays: [1, 7],
        },
}]);

const disabledAttribute = ref([{
        contentStyle: {
            color: 'red',
        },
        contentHoverStyle: {
                cursor: 'default',
                backgroundColor: 'transparent',
        },
}]);

const homeDisplay = ref(true);
const meales = ref([]);
const message = ref('');
const c_message = ref('');

export default {
    name: "MealView",
    components: { 
        Header,
    },    
    data() {
            return {
                holidays: holidays,
                date: new Date(),
                disabledDates,
                selectedColor,
                disabledAttribute,
                // newMeal: {
                //     id: "",
                //     date: "",
                //     meal: "",
                //     created_at:0,
                // },
                message:message,
                c_message:c_message,
                timer: 1,
                interval: "",
                edit:edit,
                input_menu_display:input_menu_display,
                pick_date:pick_date,
                pick_id:pick_id,
                homeDisplay: homeDisplay,
                meales: meales,
            }
    },
    computed: {        
        fetchHolidayList() {
            return this.$store.state.holiday.holidayList;            
        },        
        holidates() {
            return this.holidays.map(day => day.date);
        },
        fetchMealdayList() {
            return this.$store.state.meal.mealList;            
        },
        attributes() {
            return [
                {
                    contentStyle: {
                        fontWeight: '700',
                        fontSize: '.9rem',
                    },
                    dates: new Date(),
                },                
                
                ...this.holidates.map(date => ({
                    highlight: 'red',
                    dates: date,
                })),

                ...this.meales,
                
            ];
        },        
    },
    watch:{
        fetchHolidayList(val){
            if (val.length>0) {
                this.holidays = val;                            
            }            
        }, 
        fetchMealdayList(val) {
            if(val.length>0) {
                this.meales =[];
                val.forEach ( (meal) => {
                    this.meales.push({
                        key: meal.id,
                        dates: meal.date,
                        dot: 'blue',
                        popover: {
                            label: meal.meal,
                        },
                    });
                });                
            }
        }
    },
    mounted() {
        this.$store.dispatch("fetchHolidayList");
        this.$store.dispatch("fetchMealdayList");
    },    
    methods: {            
        onDayClick(day) {                
            
            const now = new Date();
            const offset = new Date().getTimezoneOffset() * 60000
            const date = new Date(day.date - offset);
            let day_s = date.toISOString().split('T')[0];
            let nowday_s = now.toISOString().split('T')[0];            
            const hoo = this.holidays;
            const mhoo = this.meales;
            if( day_s >= nowday_s) {
                if ( hoo.length >0 ) {                    
                    const ix = hoo.findIndex(d => d.id === day.id);                    
                    if (ix < 0) {

                        const ixx = mhoo.findIndex(d => d.key === day.id);
                        if ( ixx >=0 ) {
                            this.pick_date= date.toISOString();
                            this.pick_id = date.toISOString().split('T')[0];
                            this.input_menu_display=1;
                            this.message = mhoo[ixx].popover.label;
                        } else {
                            this.pick_date= date.toISOString();
                            this.pick_id = date.toISOString().split('T')[0];
                            this.input_menu_display=1;
                            this.message = '';
                        }
                        this.c_message = this.message;
                    }
                }                                    
            }

        },
        
        inputMeal() {
            this.input_menu_display=0;
            
            if ((this.message != null) && (this.message != this.c_message)) {

                console.log('message :',this.message, 'c-messag :', this.c_message);
                console.log('pick : ',this.pick_id, this.pick_date);

                if ( this.c_message.length > 0 ) {
                    console.log(' old', this.message);

                    this.$store.dispatch("updateMeal", this.pick_id, this.message);
                } else {                        
                    console.log(' new', this.message);
                    var newM = {
                        id: this.pick_id,
                        date: this.pick_date,
                        meal: this.message,
                        created_at: new Date(),
                    };
                    this.$store.dispatch("addNewMeal", newM);
                }                
            }            
        },
            
        dateDisabled(date) {                
            const weekday = date.getDay()
            return weekday === 0 || weekday === 6
        },

            // startTimer() {
            //     this.interval = setInterval(() => {
            //         if (this.timer === 0) {
            //             clearInterval(this.interval)
            //             this.message="";                
            //         } else {
            //             this.timer--
            //         }             
            //     }, 700)
            // }
        }
};
</script>

<style>
.my-calendar .vc-weekday-1, .my-calendar .vc-weekday-7 {
    color: #f50303;
}

.my-calendar.vc-container {
    --day-border: 1px solid #b8c2cc;
    --day-border-highlight: 1px solid #b8c2cc;
    --day-width: 90px;
    --day-height: 90px;
    --weekday-bg: #f8fafc;
    --weekday-border: 1px solid #eaeaea;

    border-radius: 5%;
    width: 98%;
}
.tablepick {
    padding: 4%;
}
.mycal{
    padding: 10px;
}
.footer{
    padding: 20px;
}

</style>

